import { actionTypes } from './account.actions';

const initialState = {
  name: 'Guest',
  email: ' ',
  profile: '/images/tmpprofile.jpg'
}

const account = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.UPDATE_DATA:
      return {
        name: action.name,
        email: action.email,
        profile: action.profile
      }
    default:
      return state;
  }
}
export default account



