export const createStyleObject = (props) => {
  return {
    width: props.width,
    height: props.height,
    fontSize: props.fontSize,
    margin: props.margin,
    position: props.position,
    left: props.left,
    right: props.right,
    top: props.top,
    bottom: props.bottom
  }
}


